import { Card, TextField } from "@mui/material";
import React, { useState } from "react";
import "../style/privateship.css";
import axios from "axios";
import { useEffect } from "react";

const PrivateShip = (cartData) => {



  const [datas, setDatas] = useState([]);

  const deliverInfo = "GLS futrszolglattal s magncl rendels"


  console.log(datas);

  const [fullName, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [phonenumber, setPhonenumber] = useState("");
  const [city, setCity] = useState("");
  const [address, setAddress] = useState("");
  const [postalCode, setPostalCode] = useState("");
  const [payment, setPayment] = useState("");

  const [bfullName, setFullNameb] = useState("");
  const [bphonenumber, setPhonenumberb] = useState("");
  const [bcity, setCityb] = useState("");
  const [baddress, setAddressb] = useState("");
  const [bpostalCode, setPostalCodeb] = useState("");
  

  const [checked, setChecked] = useState(false);


  const [isDisabled, setIsDisabled] = useState(true);
  const [checkButton, setCheckButton] = useState(false);

  const canBeSubmitted = () => {
    return checkButton ? setIsDisabled(true) : setIsDisabled(false);
  };

  const onCheckboxClick = () => {
    setCheckButton(!checkButton);
    return canBeSubmitted();
  };




  const handleCheckChange = () => {
    setChecked(!checked);
  };


  const handleSelectChange = (e) => {
    setPayment(e.target.value);
  };

  

  Object.values(cartData?.cartData).map((x) => x);

  useEffect(() => {
    setDatas(
      cartData?.cartData?.cartData.map((item) => ({
        productId: item.id,
        productName: item.name,
        totalPrice: item.totalPrice,
        totalAmount: item.totalAmount,
      }))
    );
  }, [cartData]);


  const datak = JSON.stringify(datas, null, "\t");


  const handleSubmit = async (e) => {
    if (
      deliverInfo &&
      datak &&
      email &&
      phonenumber &&
      fullName &&
      city &&
      address &&
      postalCode !== ""
    ) {
      e.preventDefault();
      console.log({
        deliverInfo,
        datak,
        email,
        phonenumber,
        fullName,
        city,
        address,
        postalCode,
        bfullName,
        bphonenumber,
        bcity,
        baddress,
        bpostalCode,
        payment,
      });

      const body = {
        deliverInfo,
        datak,
        email,
        phonenumber,
        fullName,
        city,
        address,
        postalCode,
        bfullName,
        bphonenumber,
        bcity,
        baddress,
        bpostalCode,
        payment,
      };

      await axios
        .post("/shopmail", body, {
          headers: {
            "Content-type": "application/json",
          },
        })
        .then((res) => {
          alert("A megrendels sikeres volt");
          console.log(res);
          window.location.reload();
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      alert("Krem tltse ki a megadott mezket");
    }
  };

  console.log();

  return (
    <div>
      <form onSubmit={handleSubmit} method="post">
        <Card className="shippingCardPerson">
          <h5>Szmlzsi adatok:</h5>

          <TextField
            style={{ margin: "5px 10px 15px 10px" }}
            type="text"
            label="Teljes nv"
            value={fullName}
            required
            onChange={(e) => setFullName(e.target.value)}
          />

          <TextField
            style={{ margin: "5px 10px 15px 10px" }}
            type="text"
            label="Irnytszm"
            value={postalCode}
            required
            onChange={(e) => setPostalCode(e.target.value)}
          />

          <TextField
            style={{ margin: "5px 10px 15px 10px" }}
            type="text"
            label="Vros,Kzsg"
            value={city}
            required
            onChange={(e) => setCity(e.target.value)}
          />

          <TextField
            style={{ margin: "5px 10px 15px 10px" }}
            type="text"
            label="Utca hzszm"
            value={address}
            required
            onChange={(e) => setAddress(e.target.value)}
          />

          <TextField
            style={{ margin: "5px 10px 15px 10px" }}
            type="email"
            label="Email cm"
            value={email}
            required
            onChange={(e) => setEmail(e.target.value)}
          />

          <TextField
            style={{ margin: "5px 10px 15px 10px" }}
            type="text"
            label="Telefonszm"
            value={phonenumber}
            required
            onChange={(e) => setPhonenumber(e.target.value)}
          />
        </Card>

        <input
          className="checkInput"
          type="checkbox"
          checked={checked}
          onChange={handleCheckChange}
        ></input>
        <span>*Krem jellje be a ngyzetet,ha eltr a szlltsi adat!</span>

        <div className={`checkbox ${!checked ? "checkbox--active" : ""}`}>
          <Card className="shippingCardPerson" style={{ marginTop: "30px" }}>
            <h5>Szlltsi adatok:</h5>
            <span style={{ margin: "15px", fontSize: "14px" }}>
              *Krjk a szmlzsi adatokat abban esetben tltse ki ha nem
              egyezik szlltsi adatokkal!
            </span>
            <TextField
              style={{ margin: "5px 10px 15px 10px" }}
              type="text"
              label="Teljes nv"
              value={bfullName}
              onChange={(e) => setFullNameb(e.target.value)}
            />

            <TextField
              style={{ margin: "5px 10px 15px 10px" }}
              type="text"
              label="Irnytszm"
              value={bpostalCode}
              onChange={(e) => setPostalCodeb(e.target.value)}
            />

            <TextField
              style={{ margin: "5px 10px 15px 10px" }}
              type="text"
              label="Vros,Kzsg"
              value={bcity}
              onChange={(e) => setCityb(e.target.value)}
            />

            <TextField
              style={{ margin: "5px 10px 15px 10px" }}
              type="text"
              label="Utca hzszm"
              value={baddress}
              onChange={(e) => setAddressb(e.target.value)}
            />

            <TextField
              style={{ margin: "5px 10px 15px 10px" }}
              type="text"
              label="Telefonszm"
              value={bphonenumber}
              onChange={(e) => setPhonenumberb(e.target.value)}
            />
          </Card>
        </div>
        
        <h5 style={{marginTop:"50px"}}>Fizetsi md:</h5>
        <select
          className="selectInner"
          value={payment}
          onChange={handleSelectChange}
        >
          <option value='Banki tutals'>Banki tutals</option>
          <option value='Utnvt'>Utnvt</option>
        </select>

        <div style={{margin:"16px"}}>
          <input type="checkbox" onClick={onCheckboxClick} />
          <span>Egyetrtek az ltalnos zleti felttelekkel s elfogadom a szemlyes adatok feldolgozst a megrendelsek feldolgozshoz. 
            Tudomsul veszem, hogy a megrendels leads fizetsi ktelezettsget von maga utn.**
          </span>
        </div>
        <button
        type="submit" disabled={isDisabled} className="cartButton">
          Megrendels
        </button>
      </form>
    </div>
  );
};

export default PrivateShip;
